/* poppins-300 - latin */
@font-face {
	font-family: 'Poppins';
	font-style: normal;
	font-weight: 300;
	src: url('./poppins-v15-latin/poppins-v15-latin-300.eot'); /* IE9 Compat Modes */
	src: local(''),
	url('./poppins-v15-latin/poppins-v15-latin-300.eot?#iefix') format('embedded-opentype'), /* IE6-IE8 */
	url('./poppins-v15-latin/poppins-v15-latin-300.woff2') format('woff2'), /* Super Modern Browsers */
	url('./poppins-v15-latin/poppins-v15-latin-300.woff') format('woff'), /* Modern Browsers */
	url('./poppins-v15-latin/poppins-v15-latin-300.ttf') format('truetype'), /* Safari, Android, iOS */
	url('./poppins-v15-latin/poppins-v15-latin-300.svg#Poppins') format('svg'); /* Legacy iOS */
}
